<template>
  <div class="coupon">
    <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6" :title-active-color="'#ef5852'" @click="onClick">
      <van-tab v-for="(item,index) in tabList" :key="index" :title="item.text"></van-tab>
    </van-tabs>
    <div class="mescroll coupon-list no-list" id="couponList">
      <div id="couponListWrap">
        <div class="coupon-list-item" v-for="(couponItem,index) in couponList" :key="index" @click="choose(couponItem)">
          <div class="coupon-list-item-left">
            <p class="title">
              <!-- <span :style="couponItem.coupon_state!=1?'background:#d8d8d8;':''"></span> -->
              {{couponItem.ytitle}}
            </p>
            <p class="time">{{String(couponItem.endDate).substring(0,10)}}</p>
            <p class="text">{{couponItem.status=='0'?'':couponItem.status=='1'?'':''}}
              <!-- | {{couponItem.coupon_note}} -->
            </p>
          </div>
          <div class="coupon-list-item-right">
            <p class="price">
              {{couponItem.ymoney}}
              <span></span>
            </p>
            <p class="text">{{couponItem.yamt}}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from "vant";
import MeScroll from "mescroll.js";
import { getCouponList } from "@/api";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  data() {
    return {
      tabList: [
        {
          text: " ",
          value: ""
        },
        {
          text: "",
          value: "0"
        },
        {
          text: "",
          value: "1"
        },
        {
          text: "",
          value: "9"
        }
      ],
      mescroll: null,
      activeType: 0,
      active: 0,
      couponList: []
    };
  },

  methods: {
    onClick(name, title) {
      this.activeType = this.tabList[name].value;
      this.mescroll.scrollTo(0, 0);
      this.mescroll.resetUpScroll();
    },
    choose(item) {
      if (this.$route.query.type == "1") {
        if (!(item.status == "0" && item.yamt <= this.$route.query.money)) {
          this.$toast("");
          return;
        }
        sessionStorage.setItem("couponDetail", JSON.stringify(item));
        this.$router.back();
      }
    },
    loadData(pageNum, pageSize, successCallback, errorCallback) {
      let dataObj = {
        pagesize: pageSize,
        pagenum: pageNum,
        status: this.activeType
      };
      getCouponList(dataObj)
        .then(res => {
          console.log(res.data);
          if (res.code == 0) {
            successCallback && successCallback(res.data);
          } else {
            this.$toast.fail(res.msg);
          }
        })
        .catch(err => {
          errorCallback && errorCallback();
          console.log(err);
        });
    },
    startMescroll() {
      this.mescroll = new MeScroll("couponList", {
        //vuemountedmescroll,id
        down: {
          htmlContent:
            '<p class="downwarp-progress" ></p><p class="downwarp-tip"> </p>'
        },
        up: {
          callback: this.upCallback, //
          //,
          isBounce: false, //ios,(,): http://www.mescroll.com/qa.html#q10
          page: { size: 10 }, //8,10
          empty: {
            //
            warpId: "couponListWrap",
            icon: require("@/assets/img/nodata.png"),
            tip: ""
            //						  	btntext : " >" ,
            //						  	btnClick : function() {
            //						  		alert("");
            //						  	}
          },
          scrollbar: { use: false },
          htmlLoading:
            '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip" >..</p>',
          htmlNodata: '<p class="upwarp-nodata">--  --</p>'
        }
      });
    },
    // page = {num:1, size:10}; num: ,1; size:,10
    upCallback: function(page) {
      //
      let that = this;
      that.loadData(
        page.num,
        page.size,
        function(curPageData) {
          // (clearIdclearEmptyId)
          if (page.num == 1) that.couponList = [];
          //
          that.couponList = that.couponList.concat(curPageData.list);
          //,;
          //mescroll,,;,;
          //():  totalSize
          that.mescroll.endBySize(curPageData.list.length, curPageData.count); //(, )
        },
        function() {
          //,;
          that.mescroll.endErr();
        }
      );
    }
  },
  mounted() {
    document.title = "";
    this.startMescroll();
  }
};
</script>
<style lang="scss" scoped>
.coupon {
  height: 100%;
  .acitive {
    font-size: 15px;
    font-weight: bold;
  }
  &-list {
    &-item {
      margin: 15px;
      border-radius: 5px;
      display: flex;

      background: #fff;
      padding: 15px;
      box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.1);
      &-left {
        flex: 2;
        .title {
          height: 25px;
          font-weight: bold;
          span {
            background: $base-color;
            color: #fff;
            font-size: 11px;
            display: inline-block;
            height: 25px;
            line-height: 25px;
            width: 45px;
            text-align: center;
            border-radius: 5px;
            margin-right: 5px;
          }
        }
        .time {
          line-height: 25px;
          font-size: 15px;
          margin-top: 10px;
          color: #000;
        }
        .text {
          font-size: 12px;
          color: #999;
          margin-top: 15px;
        }
      }
      &-right {
        text-align: center;
        .price {
          font-size: 25px;
          font-weight: bold;
          span {
            font-size: 15px;
          }
        }
        .text {
          font-size: 15px;
        }
      }
    }
  }
}
.no {
  &-list {
    height: calc(100% - 46px);
    background: #efefef;
  }
}
</style>
<style lang="scss">
</style>
